import React from 'react'
import PaginationStyles from './styles/PaginationStyles'
import gql from 'graphql-tag'

const PAGINATION_QUERY = gql`
    query PAGINATION_QUERY {
        itemsConnection {
            aggregate {
                count
            }
        }
    }
`

const Pagination = () => {
    return (
        <PaginationStyles>
            <p>Pagination</p>
        </PaginationStyles>
    )
}

export default Pagination;
